@import "../../../mixin.scss";
.contacts {
	display: flex;
	flex-grow: 1;
	background: #1C1D25;
	&__container {
		display: flex;
		justify-content: center;
		flex-grow: 1;
		max-width: 1050px;
		margin: 0 auto;
		padding: 0 15px;
	}
	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		flex-grow: 1;
		row-gap: 30px;
		@include adaptiveValue("row-gap",30,20,462,320,1);
	}
	&__text_container{
		display: flex;
		flex-direction: column;
		align-items: center;
		row-gap: 15px;
		text-align: center;
	}
	&__title {
		opacity: 0;
		animation: animateTitle 0.7s ease 1 1.1s forwards;
		font-size: 50px;
		font-weight: 700;
	}
	&__text{
		font-size: 23px;
	}
