	&__form {
		display: flex;
		flex-direction: column;
		align-items: center;
		// row-gap: 20px;
		width: 430px;
		input {
			margin: 0px 0px 20px 0px;
			@include adaptiveValue("margin-bottom",20,10,462,320,1);
		}
		@include adaptiveValue("width",430,290,462,320,1);
		input:-webkit-autofill,
		input:-webkit-autofill:hover, 
		input:-webkit-autofill:focus,
		textarea:-webkit-autofill,
		textarea:-webkit-autofill:hover,
		textarea:-webkit-autofill:focus,
		select:-webkit-autofill,
		select:-webkit-autofill:hover,
		select:-webkit-autofill:focus {
		-webkit-text-fill-color: white;
		-webkit-box-shadow: inset 3px 4px 7px #0d0e12, inset -3px -4px 3px #313341, 0 0 0px 1000px #1C1D25 inset; 
		box-shadow: inset 3px 4px 7px #0d0e12, inset -3px -4px 3px #313341, 0 0 0px 1000px #1C1D25 inset;
		border: none;
		// color-scheme: dark; // Цвет подложки автозаполнения, запихать в input
		}
		input {
			position: relative;
			font-family: 'League Spartan', sans-serif;
			font-size: 20px;
			font-weight: 500;
			width: 100%;
			border: none;
			border-radius: 13px;
			background: #1C1D25;
			padding: 15px 20px;
			outline: none;
			color: white;
			caret-color: red;
			color-scheme: dark; // Цвет подложки автозаполнения, запихать в input
			box-shadow: inset 3px 4px 7px #0d0e12, inset -3px -4px 3px #313341;
			// -webkit-appearance: none; 
			// appearance: none;
			&::placeholder {
				position: absolute;
				top: 19px;
				left: 20px;
				color: rgba(176, 178, 195, 0.5);
				font-size: 14px;
				font-weight: 600;
				transition: all ease 0.4s;
			 }
			 &:focus{
				&::placeholder {
					left: 50px;
					opacity: 0;
					// transform: scale(0);
				 }
			 }
		}
	}
	&__block_input{
		position: relative;
		width: 100%;
	}
	&__input_name {
		&:focus ~ .contacts__label_name,
		&:valid ~ .contacts__label_name {
			opacity: 1;
			transform: scale(1);
		}
	}
	&__input_email {
		&:focus ~ .contacts__label_email,
		&:valid ~ .contacts__label_email {
			opacity: 1;
			transform: scale(1);
		}
	}
	&__textarea {
		&:focus ~ .contacts__label_message,
		&:valid ~ .contacts__label_message {
			opacity: 1;
			transform: scale(1);
		}
	}
	&__label_name {
		position: absolute;
		opacity: 0;
		top: -6px;
		left: 10px;
		font-size: 14px;
		font-weight: 600;
		color: #F9C847;
		transform: scale(0);
		transition: all ease .5s;
	}
	&__label_email {
		position: absolute;
		opacity: 0;
		top: -6px;
		left: 10px;
		font-size: 14px;
		font-weight: 600;
		color: #F9C847;
		transform: scale(0);
		transition: all ease 0.5s;
	}
	&__label_message {
		position: absolute;
		opacity: 0;
		top: -6px;
		left: 10px;
		font-size: 14px;
		font-weight: 600;
		color: #F9C847;
		transform: scale(0);
		transition: all ease 0.5s;
	}	
	&__textarea {
		position: relative;
		font-family: 'League Spartan', sans-serif;
		font-size: 20px;
		font-weight: 500;
		width: 100%;
		min-height: 150px;
		resize:vertical;
		border: none;
		border-radius: 13px;
		background: #1C1D25;
		padding: 7px 10px;
		padding: 15px 20px;
		outline: none;
		color: white;
		caret-color: red;
		box-shadow: inset 3px 4px 7px #0d0e12, inset -3px -4px 3px #313341;
		@include adaptiveValue("min-height",150,120,462,320,1);
		// -webkit-appearance: none; 
		// appearance: none;
		&::placeholder {
			position: absolute;
			top: 19px;
			left: 20px;
			color: rgba(176, 178, 195, 0.5);
			font-size: 14px;
			font-weight: 600;
			transition: all ease 0.4s;
		 }
		 &:focus{
			&::placeholder {
				left: 50px;
				opacity: 0;
				// transform: scale(0);
			 }
		 }
	}
	&__form_button {
		display: inline-block;
		font-family: 'League Spartan', sans-serif;
		font-size: 16px;
		font-weight: 700;
		opacity: 0;
		color: black;
		// color: white;
		padding: 9px 15px 6px;
		border-radius: 30px;
		margin: 0px 0px 15px 0px;
		background: linear-gradient(to bottom, #F9C847 , #f9c64782);
		// background: linear-gradient(to bottom, rgba(28,29,37,1) 0%,rgba(249,200,71,1) 100%);
		transition: all linear 0.05s;
		animation: animateButton 0.7s ease 1 1.3s forwards;
		&:active {
			box-shadow: #f9c64752 0px 16px 15px -2px;
			transform: translate(0px,2px);
			}
			&:hover {
			color: white;
			}
		cursor: pointer;
	}
}
@keyframes animateTitle {
	0% {transform: translate(0,20px);}
	100% {transform: translate(0px,0px);opacity: 1}
}
@keyframes animateButton {
	0% {transform: translate(0,20px);}
	100% {transform: translate(0px,0px);opacity: 1;box-shadow: #f9c64752 0px 20px 15px 0px;}
}


